import clsx from 'clsx';
import React from 'react';
import { Await } from 'react-router';

import { Spinner } from './Spinner.js';

export interface ImageData {
  src: string;
  alt: string;
  width?: number;
  height?: number;
  className?: string;
  loading: 'eager' | 'lazy';
}

interface LazyImageProps {
  image: Promise<ImageData>;
  className?: string;
  fallback?: React.ReactNode;
}

interface LazyGalleryProps {
  images: Array<Promise<ImageData>>;
  className?: string;
  imageClass?: string;
  fallback?: React.ReactNode;
}

const Image: React.FC<ImageData> = ({ src, alt, width, height, className, loading = 'lazy' }) => {
  return <img src={src} alt={alt} width={width} height={height} className={className} loading={loading} decoding="async" />;
};

export const imageLoader = async (filenames: Record<string, () => Promise<unknown>>) => {
  return Object.keys(filenames).map(filePath => filePath.replace(/.*\/(.*)/, '$1'));
};

export function LazyImage({ image, className, fallback = <Spinner /> }: LazyImageProps) {
  return (
    <React.Suspense fallback={fallback}>
      <TypedAwait<ImageData> resolve={image} errorElement={<Spinner className="text-red-500" />}>
        {(imageData: ImageData) => <Image {...imageData} className={clsx(imageData.className, className)} />}
      </TypedAwait>
    </React.Suspense>
  );
}

export function LazyGallery({ images, className, imageClass, fallback = <Spinner className="h-[180px]" /> }: LazyGalleryProps) {
  return (
    <div className={className}>
      {images.map((imgPromise, index) => (
        // biome-ignore lint/suspicious/noArrayIndexKey:
        <LazyImage key={index} image={imgPromise} className={imageClass} fallback={fallback} />
      ))}
    </div>
  );
}

function TypedAwait<T>({ resolve, children, ...props }: { resolve: Promise<T>; children: (data: T) => React.ReactNode; [key: string]: unknown }) {
  return (
    <Await resolve={resolve} {...props}>
      {data => children(data as T)}
    </Await>
  );
}

