import type React from 'react';

export interface SpriteIconProps<IconUrl extends string> extends React.SVGProps<SVGSVGElement> {
  url: IconUrl;
  id?: string;
}

export function SpriteIcon<IconUrl extends string>({ url, id, ...props }: SpriteIconProps<IconUrl>) {
  return (
    // biome-ignore lint/a11y/noSvgWithoutTitle:
    <svg {...props}>{id ? <use href={`${url}#${id}`} /> : <use href={`${url}`} />}</svg>
  );
}
